import React, { useEffect, useState } from 'react';
import {
  Form,
  Row,
  Col,
  message,
  Card,
  Spin,
  Icon,
  Radio,
  Divider,
  Select,
  Upload,
  Alert,
  Input,
  Typography,
  Button,
} from 'antd';
import { Link } from 'react-router-dom';
import { injectIntl } from 'react-intl';
import parse from 'html-react-parser';
import _ from 'lodash';
import {
  getProcessTypes,
  enginesByUserv2,
  processFile,
  quoteFile,
  translate,
} from './../../services';

import functions from './../../utils/functions';
import { ButtonWrapper, ColWrapper, FormItemWrapper, Div, DivError, DivButtons } from './../Styled';

import SuccessCmp from './../SuccessCmp';
import ErrorCmp from './../ErrorCmp';

const Option = Select.Option;
const Dragger = Upload.Dragger;
const { TextArea } = Input;
const { Paragraph } = Typography;

const CreateProcessForm = ({
  processes,
  engines,
  getFieldDecorator,
  getFieldsError,
  messages,
  allowedFiles,
  typeOfUser,
  changeFileID,
  form,
  setCollapsed,
  processTypes,
  subprocessTypes,
}) => {
  const [process, setProcess] = useState(false);
  const [view, setView] = useState('process');
  const [radioSelect, setRadioSelect] = useState(1);

  const [fileList, setFileList] = useState([]);
  const [showError, setShowError] = useState(false);
  const [result, setResult] = useState('Translation');
  const [uploading, setUploading] = useState(false);
  const [translating, setTranslating] = useState(false);

  const [source, setSource] = useState(null);
  const [target, setTarget] = useState(null);
  const [engine, setEngine] = useState(null);

  useEffect(() => {
    init();
  }, [processes.length, engines.length]);

  const formItemLayout = {
    labelCol:
      radioSelect === 1
        ? {
            xs: { span: 24 },
            sm: { span: 24 },
          }
        : {
            xs: { span: 24 },
            sm: { span: 10 },
          },
    wrapperCol:
      radioSelect === 1
        ? {
            xs: { span: 24 },
            sm: { span: 24 },
          }
        : {
            xs: { span: 24 },
            sm: { span: 14 },
          },
  };

  const init = () => {
    if (processes.length) {
      changeProcess(processes[0]);
    }
  };

  const initStateView = view => {
    setView(view);
    setView('process');
    setFileList([]);
    setUploading(false);
    setShowError(false);
  };

  const getSources = (pr = null) => {
    let n_array = [];
    if (!pr) pr = process;
    if (pr) {
      let array = engines.filter(el => {
        let pid = pr.id || pr.item.id;
        if (pr.parent) {
          pid = pr.parent.id;
        }
        return el.processid === pid;
      });

      if (array.length) {
        array[0].text = functions.languages[array[0].source];

        n_array.push(array[0]);
        array.forEach(element => {
          const tmp = n_array.filter(el => {
            return el.source === element.source;
          });
          if (!tmp.length) {
            element.text = functions.languages[element.source];
            n_array.push(element);
          }
        });
      }
    }

    n_array = _.sortBy(n_array, [
      item => {
        return item.text;
      },
    ]);

    return n_array;
  };

  const getTargets = (pr = null, src = null) => {
    let n_array = [];
    if (!src) src = source;
    if (!pr) pr = process;
    if (src) {
      let array = engines.filter(el => {
        let pid = pr.id || pr.item.id;
        if (pr.parent) {
          pid = pr.parent.id;
        }
        return el.processid === pid && el.source === src.source;
      });

      if (array.length) {
        array[0].text = functions.languages[array[0].target];

        n_array.push(array[0]);
        array.forEach(element => {
          const tmp = n_array.filter(el => {
            return el.target === element.target;
          });
          if (!tmp.length) {
            element.text = functions.languages[element.target];
            n_array.push(element);
          }
        });
      }
    }

    n_array = _.sortBy(n_array, [
      item => {
        return item.text;
      },
    ]);
    return n_array;
  };

  const getEngines = (pr = null, src = null, tgt = null) => {
    let n_array = [];
    if (!src) src = source;
    if (!tgt) tgt = target;
    if (!pr) pr = process;
    if (src && tgt) {
      const nengines = JSON.parse(JSON.stringify(engines));
      let array = nengines.filter(el => {
        let pid = pr.id || pr.item.id;
        if (pr.parent) {
          pid = pr.parent.id;
        }
        return el.processid === pid && el.source === src.source && el.target === tgt.target;
      });

      if (array.length) {
        array[0].text = array[0].name;
        n_array.push(array[0]);
        array.forEach(element => {
          const tmp = n_array.filter(el => {
            return el.name === element.name;
          });
          if (!tmp.length) {
            element.text = element.name;
            n_array.push(element);
          }
        });
      }
    }

    return n_array;
  };

  const changeProcess = value => {
    setProcess(value);
    const src = JSON.parse(JSON.stringify(getSources(value)));
    if (src.length) {
      setSource(src[0]);
      const tgt = JSON.parse(JSON.stringify(getTargets(value, src[0])));
      if (tgt.length) {
        setTarget(tgt[0]);
        const eng = JSON.parse(JSON.stringify(getEngines(value, src[0], tgt[0])));
        if (eng.length) {
          setEngine(eng[0]);
        }
      }
    } else {
      setSource(null);
      setTarget(null);
      setEngine(null);
    }
  };

  const onChange = e => {
    setRadioSelect(e.target.value);
  };

  const onChangeProcess = value => {
    const aux = processes.filter(el => el.item.variableName === value);
    if (aux.length) {
      changeProcess(aux[0]);
    }
  };

  const changeSource = id => {
    const src = engines.filter(el => {
      return el.id === parseInt(id);
    });
    if (src.length) {
      setSource(JSON.parse(JSON.stringify(src[0])));
      const tgt = JSON.parse(JSON.stringify(getTargets(process, src[0])));
      if (tgt.length) {
        setTarget(tgt[0]);
        const eng = JSON.parse(JSON.stringify(getEngines(process, src[0], tgt[0])));
        if (eng.length) {
          setEngine(eng[0]);
        }
      }
    }
  };

  const changeTarget = id => {
    const tgt = engines.filter(el => {
      return el.id === parseInt(id);
    });
    if (tgt.length) {
      setTarget(tgt[0]);
      const eng = JSON.parse(JSON.stringify(getEngines(process, source, tgt[0])));
      if (eng.length) {
        setEngine(eng[0]);
      }
    }
  };

  const changeEngine = id => {
    const eng = getEngines().filter(el => {
      return el.id === parseInt(id);
    });
    if (eng.length) {
      setEngine(eng[0]);
    }
  };

  const options = () => {
    let array = [];
    if (processes.length) {
      array = processes.map(d => {
        if (processTypes !== undefined && processTypes.length) {
          if (d.parent) {
            let p = false;
            let s = false;
            processTypes.forEach(el => {
              if (el.id === d.parent.id) {
                p = true;
                return;
              }
            });

            if (subprocessTypes !== undefined && subprocessTypes.length) {
              subprocessTypes.forEach(el => {
                if (el.id === d.item.id) {
                  s = true;
                  return;
                }
              });
            }

            if (p && s) {
              return (
                <Option key={d.item.id} value={d.item.variableName}>
                  {d.item.name.replace(/^\w/, c => c.toUpperCase())}
                </Option>
              );
            } else {
              return null;
            }
          } else {
            let p = false;
            processTypes.forEach(el => {
              if (el.id === d.item.id) {
                p = true;
                return;
              }
            });
            if (p) {
              return (
                <Option key={d.item.id} value={d.item.variableName}>
                  {d.item.name.replace(/^\w/, c => c.toUpperCase())}
                </Option>
              );
            } else {
              return null;
            }
          }
        } else {
          return (
            <Option key={d.item.id} value={d.item.variableName}>
              {d.item.name.replace(/^\w/, c => c.toUpperCase())}
            </Option>
          );
        }
      });
    }
    return array;
  };

  const dprops = {
    onRemove: file => {
      const index = fileList.indexOf(file);
      const newFileList = fileList.slice();
      newFileList.splice(index, 1);
      setFileList(newFileList);
    },

    beforeUpload: file => {
      const isLt2M = file.size / 1024 / 1024 < 500;
      if (!functions.checkFile(file, allowedFiles)) {
        message.error(messages.process.error.uploadOne.replace('allowedFiles', allowedFiles));
      } else if (!isLt2M) {
        message.error(messages.process.error.uploadTwo);
      }
      return false;
    },
    onChange: info => {
      let aux = [];
      info.fileList.map(item => {
        if (functions.checkFile(item, allowedFiles)) {
          aux.push(item);
        }
        return aux;
      });

      setFileList(aux);
    },
    multiple: true,
    fileList,
  };

  const handleSubmit = e => {
    e.preventDefault();

    setShowError(false);
    setView('process');

    form.validateFields(async (err, values) => {
      if (!err) {
        if (radioSelect === 1) {
          if (fileList.length) {
            let form = {
              processId: engine.name,
              processName: process.parent ? process.parent.variableName : process.item.variableName,
              processType: values.process,
              engineId: engine.id,
              engineName: engine.name,
              engineDomain: engine.domain,
              engineSource: engine.source,
              engineTarget: engine.target,
              files: fileList,
            };
            setUploading(true);
            if (typeOfUser === 2 || typeOfUser === 3) {
              try {
                const { data } = await quoteFile(form);
                console.log(data);
                setUploading(false);
                setView('success');
                setFileList([]);
                changeFileID(data.fileId);
              } catch (error) {
                message.error(error.message);
                setUploading(false);
                setView('error');
              }
            } else {
              try {
                const { data } = await processFile(form);
                console.log(data);
                setUploading(false);
                setView('success');
                setFileList([]);
                changeFileID(data.fileId);
              } catch (error) {
                message.error(error.message);
                setUploading(false);
                setView('error');
              }
            }
          } else {
            setShowError(true);
          }
        } else {
          const form = {
            engine: engine,
            processType: values.process,
            text: values.text,
          };

          try {
            setTranslating(true);
            const response = await translate({ ...form });
            setResult(response.data.data[0][0].tgt);
            setTranslating(false);
          } catch (error) {
            setTranslating(false);
            message.error(error.message);
          }
        }
      }
    });
  };
  return (
    <div>
      {view === 'process' && (
        <Row>
          <Col>
            <Form onSubmit={handleSubmit}>
              <Row type="flex" justify="center">
                <ColWrapper xs={24} md={12}>
                  <Radio.Group onChange={onChange} value={radioSelect}>
                    <Radio value={1}>Upload File</Radio>
                    <Radio value={0}>Translate Text</Radio>
                  </Radio.Group>
                </ColWrapper>
                <ColWrapper md={12}>
                  <Link to="/dashboard/process-services">
                    <ButtonWrapper type="primary" icon="rollback">
                      {messages.process.listProcess}
                    </ButtonWrapper>
                  </Link>
                </ColWrapper>
                <Divider />
                <ColWrapper xs={24} md={radioSelect === 1 ? 12 : 24}>
                  <Row type="flex" justify="center" align="middle">
                    <Col xs={24}>
                      <Row
                        type="flex"
                        justify="space-between"
                        className={radioSelect === 1 ? '' : 'translate-mode'}
                      >
                        <Col xs={24} md={{ span: radioSelect === 1 ? 24 : 5 }}>
                          <FormItemWrapper
                            flexCenter={radioSelect === 1 ? false : true}
                            {...formItemLayout}
                            mode={radioSelect}
                            label={messages.process.process}
                            hasFeedback
                          >
                            {getFieldDecorator('process', {
                              initialValue: process ? process.item.variableName : null,
                              disabled: !processes.length,
                              rules: [
                                {
                                  required: true,
                                  message: messages.process.rules.process.required,
                                },
                              ],
                            })(
                              <Select onChange={onChangeProcess} autofocus="true">
                                {options()}
                              </Select>
                            )}
                          </FormItemWrapper>
                        </Col>
                        <Col
                          xs={24}
                          md={{
                            span: radioSelect === 1 ? 24 : 5,
                          }}
                        >
                          <FormItemWrapper
                            flexCenter={radioSelect === 1 ? false : true}
                            {...formItemLayout}
                            mode={radioSelect}
                            label={messages.process.source}
                            hasFeedback
                          >
                            {getFieldDecorator('source', {
                              initialValue: source ? source.text : null,
                              rules: [
                                {
                                  required: true,
                                  message: messages.process.rules.source.required,
                                },
                              ],
                            })(
                              <Select disabled={!getSources().length} onChange={changeSource}>
                                {getSources().map(element => {
                                  return <Option key={element.id}>{element.text}</Option>;
                                })}
                              </Select>
                            )}
                          </FormItemWrapper>
                        </Col>
                        <Col
                          xs={24}
                          md={{
                            span: radioSelect === 1 ? 24 : 5,
                          }}
                        >
                          <FormItemWrapper
                            flexCenter={radioSelect === 1 ? false : true}
                            {...formItemLayout}
                            mode={radioSelect}
                            label={messages.process.target}
                            hasFeedback
                          >
                            <Select
                              value={target ? target.text : null}
                              onChange={changeTarget}
                              disabled={!getTargets().length}
                            >
                              {getTargets().map(element => {
                                return <Option key={element.id}>{element.text}</Option>;
                              })}
                            </Select>
                          </FormItemWrapper>
                        </Col>
                        <Col
                          xs={24}
                          md={{
                            span: radioSelect === 1 ? 24 : 5,
                          }}
                        >
                          <FormItemWrapper
                            flexCenter={radioSelect === 1 ? false : true}
                            {...formItemLayout}
                            mode={radioSelect}
                            label="Team"
                            hasFeedback
                          >
                            <Select
                              value={engine ? engine.text : null}
                              onChange={changeEngine}
                              disabled={!getEngines().length}
                            >
                              {getEngines().map(element => {
                                return <Option key={element.id}>{element.text}</Option>;
                              })}
                            </Select>
                          </FormItemWrapper>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                </ColWrapper>
                <ColWrapper xs={24} md={radioSelect === 1 ? 12 : 24}>
                  <Row>
                    <Col xs={24}>
                      {radioSelect === 1 && (
                        <FormItemWrapper>
                          <Div error={showError && !fileList.length}>
                            <Dragger {...dprops}>
                              <p className="ant-upload-drag-icon">
                                <Icon type="inbox" />
                              </p>
                              <p className="ant-upload-text">{messages.process.drag}</p>
                            </Dragger>

                            {showError && !fileList.length ? (
                              <Alert
                                style={{ margin: 5 }}
                                message={messages.process.alert}
                                type="error"
                                showIcon
                              />
                            ) : null}
                          </Div>
                        </FormItemWrapper>
                      )}
                      {radioSelect === 0 && (
                        <React.Fragment>
                          <Row>
                            <Col xs={24} md={12}>
                              <FormItemWrapper style={{ marginTop: 30 }} hasFeedback>
                                {getFieldDecorator('text', {
                                  rules: [
                                    {
                                      required: true,
                                    },
                                  ],
                                })(<TextArea className="language-textarea" autosize />)}
                              </FormItemWrapper>
                            </Col>
                            <Col xs={24} md={12}>
                              <FormItemWrapper>
                                <div>
                                  <Paragraph
                                    className="language-textarea"
                                    style={{
                                      backgroundColor: 'rgb(240, 242, 245)',
                                      margin: '30px 0px 0 20px',
                                      minHeight: 80,
                                      padding: '0px 10px 0 10px',
                                    }}
                                  >
                                    {parse(result.replace(/(?:\r\n|\r|\n)/g, '<br />'))}
                                  </Paragraph>
                                </div>
                              </FormItemWrapper>
                            </Col>
                          </Row>
                        </React.Fragment>
                      )}
                    </Col>
                  </Row>
                </ColWrapper>
              </Row>
              <Row>
                <Col xs={24}>
                  {radioSelect === 1 && (
                    <FormItemWrapper
                      style={{
                        display: 'flex',
                        justifyContent: 'center',
                      }}
                    >
                      <Button
                        style={{ marginTop: 20 }}
                        icon="file"
                        type="primary"
                        htmlType="submit"
                        size="large"
                        loading={uploading}
                        disabled={
                          !getSources().length ||
                          !target ||
                          !source ||
                          functions.hasErrors(getFieldsError())
                        }
                      >
                        {uploading ? messages.process.uploading : messages.process.uploadingLoad}
                      </Button>
                    </FormItemWrapper>
                  )}
                  {radioSelect === 0 && (
                    <FormItemWrapper
                      style={{
                        display: 'flex',
                        justifyContent: 'center',
                      }}
                    >
                      <Button
                        style={{ marginTop: 20 }}
                        icon="global"
                        type="primary"
                        htmlType="submit"
                        size="large"
                        loading={translating}
                      >
                        {translating ? messages.process.translating : messages.process.translate}
                      </Button>
                    </FormItemWrapper>
                  )}
                </Col>
              </Row>
            </Form>
          </Col>
        </Row>
      )}

      {view === 'error' && (
        <React.Fragment>
          <Row>
            <Col>
              <Link to="/dashboard/process-services">
                <ButtonWrapper type="primary" icon="rollback">
                  {messages.process.listProcess}
                </ButtonWrapper>
              </Link>
            </Col>
          </Row>
          <Row>
            <Col>
              <DivError>
                <ErrorCmp messages={messages} />
                <DivButtons>
                  <Button
                    onClick={() => {
                      initStateView('process');
                    }}
                    type="primary"
                    size="large"
                  >
                    {messages.process.tryAgain}
                  </Button>
                </DivButtons>
              </DivError>
            </Col>
          </Row>
        </React.Fragment>
      )}

      {view === 'success' && (
        <React.Fragment>
          <Row>
            <Col>
              <Link to="/dashboard/process-services">
                <ButtonWrapper
                  type="primary"
                  icon="rollback"
                  onClick={() => {
                    initStateView('list');
                  }}
                >
                  {messages.process.listProcess}
                </ButtonWrapper>
              </Link>
            </Col>
          </Row>
          <Row>
            <Col>
              <DivError>
                <SuccessCmp messages={messages} />
                <DivButtons>
                  <Button
                    onClick={() => {
                      initStateView('process');
                    }}
                    type="primary"
                    size="large"
                  >
                    {messages.process.another}
                  </Button>
                </DivButtons>
              </DivError>
            </Col>
          </Row>
        </React.Fragment>
      )}
    </div>
  );
};

const CreateProcessFormCmp = ({
  form: { getFieldDecorator, getFieldsError },
  intl: { messages },
  form,
  allowedFiles,
  setCollapsed,
  changeFileID,
}) => {
  const [fetchingProcessTypes, setFetchingProcessTypes] = useState(false);
  const [fetchingEngines, setFetchingEngines] = useState(false);
  const [subprocessTypes, setSubprocessTypes] = useState([]);
  const [processTypes, setProcessTypes] = useState([]);
  const [engines, setEngines] = useState([]);
  const [processes, setProcesses] = useState([]);

  useEffect(() => {}, []);

  useEffect(() => {
    fetchProcessTypes();
    fetchEngines();
    setCollapsed(true);
    return () => {
      setCollapsed(false);
    };
  }, []);

  const fetchProcessTypes = async () => {
    try {
      setFetchingProcessTypes(true);
      const { data } = await getProcessTypes();
      const array = [];
      data.forEach(element => {
        if (element.SubprocessTypes && element.SubprocessTypes.length) {
          element.SubprocessTypes.forEach(item => {
            array.push({
              type: 'subprocess',
              item: item,
              parent: element,
            });
          });
        } else {
          array.push({
            type: 'process',
            item: element,
          });
        }
      });

      setProcesses(array);
      setFetchingProcessTypes(false);
    } catch (error) {
      setFetchingProcessTypes(false);
      message.error(error.message);
    }
  };

  const fetchEngines = async () => {
    try {
      setFetchingEngines(true);
      const {
        data: { subprocessTypes, processTypes, engines },
      } = await enginesByUserv2();
      setSubprocessTypes(subprocessTypes);
      setProcessTypes(processTypes);
      setEngines(engines);
      setFetchingEngines(false);
    } catch (error) {
      setFetchingEngines(false);
      message.error(error.message);
    }
  };

  return (
    <Spin spinning={fetchingEngines || fetchingProcessTypes}>
      <Card>
        <CreateProcessForm
          processes={processes}
          engines={engines}
          processTypes={processTypes}
          subprocessTypes={subprocessTypes}
          getFieldDecorator={getFieldDecorator}
          getFieldsError={getFieldsError}
          messages={messages}
          allowedFiles={allowedFiles}
          form={form}
          changeFileID={changeFileID}
          setCollapsed={setCollapsed}
        />
      </Card>
    </Spin>
  );
};

const WrapperCmp = Form.create()(injectIntl(CreateProcessFormCmp));

export default WrapperCmp;
